body {
  margin: 0;
  font-family: Arial, sans-serif;
  background-color: #c8b1f11c;
}

.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 15px 20px;
  background: white;
  border-bottom: 2px solid #ddd;
}

.logo {
  width: 120px;
  height: auto;
}

.nav-links {
  display: flex;
  gap: 15px;
}

.nav-item, .login-button {
  text-decoration: none;
  color: black;
  padding: 10px 20px;
  border-radius: 5px;
  transition: 0.3s;
}

.login-button {
  background: #4B0082;
  color: white;
}

.login-button:hover {
  background: #6A0DAD;
}

.download-button {
    display: inline-block;
    background-color: #4B0082;
    color: white;
    padding: 10px 15px;
    border-radius: 5px;
    text-decoration: none;
    margin-top: 10px;
  }
  
  .download-button:hover {
    background-color: #6A0DAD;
  }

.intro-section {
    text-align: center;
    margin-bottom: 30px;
    padding: 20px;
  }


.reports-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin: 0 10%;
  }
  
  .report-block {
    display: flex;
    align-items: center;
    width: 100%;
    background: white;
    margin: 15px 0;
    padding: 15px;
    border-radius: 10px;
    box-shadow: 2px 2px 10px rgba(0, 0, 0, 0.1);
    text-decoration: none;
    color: black;
    transition: transform 0.3s;
  }
  
  .report-block:hover {
    transform: scale(1.02);
    box-shadow: 4px 4px 15px rgba(0, 0, 0, 0.2);
  }
  
  .report-image {
    width: 40%;
    border-radius: 10px;
  }
  
  .report-content {
    padding-left: 15px;
  }

  .report-detail {
    padding: 20px;
    max-width: 80%;
    margin: auto;
  }
  
  .report-layout {
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: justify;
  }
  
  .report-detail-image {
    width: 60%;
    height: auto;
    max-width: 500px;
    border-radius: 10px;
    margin-bottom: 20px;
  }
  
  .report-content {
    font-size: 1rem;
    line-height: 1.6;
    max-width: 80%;
  }

  .auth-container {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 80vh;
    background-color: #E6E6FA;
  }
  
  .auth-box {
    background: white;
    padding: 30px;
    border-radius: 10px;
    box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.1);
    text-align: center;
    width: 300px;
  }
  
  .auth-form {
    display: flex;
    flex-direction: column;
    gap: 15px;
  }
  
  .auth-input {
    padding: 10px;
    border: 1px solid #ddd;
    border-radius: 5px;
  }
  
  .auth-button {
    background-color: #4B0082;
    color: white;
    padding: 10px;
    border: none;
    border-radius: 5px;
    cursor: pointer;
  }
  
  .auth-button:hover {
    background-color: #6A0DAD;
  }
  
  .toggle-text {
    margin-top: 10px;
    color: #4B0082;
    cursor: pointer;
  }

  .dashboard-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    min-height: 80vh;
    padding: 20px;
    background-color: #f9f9f9;
  }
  
  .dashboard-user-info {
    text-align: center;
    font-size: 1.5rem;
    font-weight: bold;
    margin-bottom: 20px;
    background: white;
    padding: 15px;
    border-radius: 10px;
    box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.1);
    width: 50%;
  }
  
  .dashboard-form {
    background: white;
    padding: 25px;
    border-radius: 10px;
    box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.1);
    display: flex;
    flex-direction: column;
    gap: 15px;
    width: 600px;
  }
  
  .dashboard-input, .dashboard-textarea, .dashboard-file {
    padding: 10px;
    border: 1px solid #ddd;
    border-radius: 5px;
  }
  
  .dashboard-textarea {
    height: 200px;
    resize: vertical;
  }
  
  .dashboard-submit, .dashboard-logout {
    background-color: #4B0082;
    color: white;
    padding: 10px;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    font-size: 1rem;
    text-align: center;
  }
  
  .dashboard-submit:hover, .dashboard-logout:hover {
    background-color: #6A0DAD;
  }
  
  .dashboard-reports {
    width: 60%;
    margin-top: 20px;
    text-align: center;
  }
  
  .report-card {
    background: white;
    padding: 15px;
    border-radius: 10px;
    box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.1);
    margin-bottom: 15px;
  }

  .downloads-container {
    display: flex;
    flex-direction: column;
    gap: 20px;
    padding: 20px;
    justify-content: center;
    align-items: center;
  }
  
  .download-block {
    background: white;
    padding: 15px;
    border-radius: 10px;
    box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.1);
    width: 80%;
    text-align: left;
    display: flex;
    flex-direction: row;
    align-items: center;
    gap: 20px;
    transition: transform 0.3s ease-in-out, box-shadow 0.3s ease-in-out;
    cursor: pointer;
  }
  
  .download-block:hover {
    transform: translateY(-5px);
    box-shadow: 0px 6px 12px rgba(0, 0, 0, 0.15);
  }
  
  .download-content {
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding-left: 20px;
  }
  
  .download-title {
    font-size: 1.5rem;
    margin-bottom: 10px;
    color: #333;
  }
  
  .download-description {
    font-size: 1rem;
    color: #555;
    margin-bottom: 10px;
  }
  
  .download-button {
    display: inline-block;
    background-color: #4B0082;
    color: white;
    padding: 10px 15px;
    border-radius: 5px;
    text-decoration: none;
    margin-top: 10px;
  }
  
  .download-button:hover {
    background-color: #6A0DAD;
  }

  /* General Button Styling */
.report-actions {
  display: flex;
  gap: 10px;
  margin-top: 10px;
}

/* Share Button */
.share-button {
  background-color: #4B0082;
  color: white;
  padding: 10px 15px;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  transition: background 0.3s ease-in-out, transform 0.2s ease-in-out;
}

.share-button:hover {
  background-color: #6A0DAD;
  transform: scale(1.05);
}

/* Collaborate Button */
.collaborate-button {
  background-color: #003cffc4;
  color: white;
  padding: 10px 15px;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  text-decoration: none;
  display: inline-block;
  transition: background 0.3s ease-in-out, transform 0.2s ease-in-out;
}

.collaborate-button:hover {
  background-color: #003cff9a;
  transform: scale(1.05);
}
